
import Vue from 'vue'
import router from './router' //  引入路由
import { Swipe, SwipeItem, Search } from 'mint-ui'; //  引入mint-ui框架
import App from './App'


Vue.config.productionTip = false; //  设置为 false 以阻止 vue 在启动时生成生产提示

import './assets/style/reset.css' //浏览器重置样式

import 'mint-ui/lib/style.css'; //  引入mint-ui框架样式
import './assets/style/mint.css' /* 修改mint默认样式 */

import 'lib-flexible/flexible'  //rem自适应



//使用Mint组件
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.component(Search.name, Search);

/* Vue 初始化 */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



