<template>
    <div id="home">

        <!--banner 图片-->
        <section class="slide-home">
            <mt-swipe :auto="3000" >
                <mt-swipe-item>
                    <img src="./../assets/images/banner1.jpg" alt="">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="./../assets/images/banner2.jpg" alt="">
                </mt-swipe-item>
                <mt-swipe-item>
                    <img src="./../assets/images/banner3.jpg" alt="">
                </mt-swipe-item>
            </mt-swipe>
        </section>

        <!--全局搜索-->
        <section class="search-met">
          <mt-search v-model="value" placeholder="输入关键字..."></mt-search>
        </section>

        <!--热点-->
        <section class="red-home">
          <div class="rd-left"><img src="./../assets/images/ico2.png" class="rdImg"></div>
          <div class="rd-left-t"><img src="./../assets/images/ico3.png" class="syImg"></div>
          <div class="scroll-wrap">
            <div class="marquee_box">
              <ul class="marquee_list" :style="{ top: -num + 'px'}" :class="{marquee_top:num}">
                <!-- 当显示最后一条的时候（num=0转换布尔类型为false）去掉过渡效果-->
                <li v-for="(item, index) in marqueeList" >
                  <span>{{item.name}}</span>
                </li>
              </ul>
            </div>
          </div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "home",
        data(){
          return{
            value:'', //搜索值
            num:0,
            marqueeList: [
              {name:'暂停销售的公告',},
              {name:'致所有足密码代言人',},
            ]

          }
        },
        created: function () {
            this.showMarquee(this.num)
        },
        methods: {
            showMarquee:function (num) {
                this.marqueeList.push(this.marqueeList[0]);
                var max = this.marqueeList.length;
                var that = this;
                var marqueetimer =  setInterval(function(){
                    num++;
                    if(num>=max ){
                    num=0;
                }
                that.num=num*30;
                }, 2000);
            }
        }

    }
</script>

<style scoped>
  .slide-home {position: relative;width: 100%;height: 4rem;overflow: hidden;}
  .mint-swipe-item img{width: 100%; height: 100%}
  .search-met{margin-top: 0.2rem;}
  .red-home{background: #fff;padding: 0.3rem 0;width: 100%;}
  .red-home .rd-left{border-right: 1px solid #e2e2e2;height: 30px; vertical-align: middle; line-height: 30px; width: 1.43rem; text-align: center;display: inline-block;}
  .rd-left .rdImg{display:inline-block;width: 0.8rem;margin-top: 0.01rem;}
  .rd-left-t{display: inline-block;margin-left: 0.22rem;height: 30px; line-height: 30px;}
  .rd-left-t .syImg{width: 0.4rem;vertical-align: middle;}
  .scroll-wrap{display: inline-block; width: 6rem; vertical-align: middle;}
  .marquee_box{display: block;position: relative;width: 100%;height: 30px;/*关键样式*/overflow: hidden;}
  .marquee_list{display: block;position: absolute;top:0;left: 0;}
  .marquee_top{transition: top 0.5s ;}/*关键样式*/
  .marquee_list li{height: 30px;/*关键样式*/line-height: 30px;/*关键样式*/font-size: 14px;padding-left: 6px;background-color: #fff;}
  .marquee_list li span{padding:0 2px;}
  .red {color: #FF0101;}
</style>



